<template>
  <v-card width="100%" class="white--text text-center">
    <v-img
      class="white--text align-center"
      height="300px"
      :src="mainImage"
      v-if="mainImage"
    >
      <v-card-text class="text-center">
        <div class="display-1 white--text text-center pa-3">{{ title }}</div>
        <div class="d-flex mb-3 justify-center" v-if="tabs">
          <div v-for="(tab, index) in tabs" :key="index">
            <v-btn
              class="white--text ml-1"
              :class="{ success: tab.active }"
              outlined
              :small="$vuetify.breakpoint.xsOnly"
              @click="changeTab(tab.id)"
            >
              {{ tab.title }}
            </v-btn>
          </div>
        </div>
      </v-card-text>
    </v-img>
  </v-card>
</template>

<script>
export default {
  props: {
    title: String,
    mainImage: String,
    tabIcon: String,
    tabs: Array
  },
  methods: {
    changeTab(tabId) {
      this.$emit("changeTab", tabId);
    }
  }
};
</script>



